body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
    font-family: Avenir;
    src: url(/TopangaGrainCompany/static/media/Avenir.eb3aa3d3.ttc) format('truetype');
  }

.NavBar {
    width: 100%;
    top: 0;
    left: 0;
    position: fixed;
    height: 65px;
    background-image: url("https://i.imgur.com/C7NSCjQ.png");
    font-family: Avenir;
    max-width: 100%;
}

.NavBar_navigation {
    display: flex;
    height: 100%;
    align-items: center;
    padding: 0 5rem;
    
}
.NavBar_Logo a {
    color: white;
    text-decoration: none;
    font-size: 1.5rem;
    
}

.NavBar_items UL {
   list-style: none;
   margin: 0;
   padding: 0;
   display: flex;
}
 
.spacer {
    flex: 1 1;
}

.NavBar_items li {
    padding: 0 3rem;
}

.NavBar_items a {
    color: white;
    text-decoration: none;
}

.NavBar_navigation a:hover,
.NavBar_items a:active {
 color: silver;
}


img {
    margin: 0 auto;

}



.video-container {
    position: relative;
    padding-bottom: 56.25%;
    padding-top: 35px;
    height: 0;
    overflow: hidden;
}

.video-container iframe {
    position: absolute;
    top:0;
    left: 0;
    width: 100%;
    height: 100%;
}

iframe {
    height: 500px;
    pointer-events: none;
    width: 100%
}

.carousel-control  {
    display: none;
}

.carousel-indicators {
    display: none;
}

@media(min-width: 700px) {
    .mobile {
      display: none;
    }
  }

  @media(max-width: 700px) {
    .desktop {
      display: none;
    }
  }

@font-face {
    font-family: Playfair Display;
